import lodash from 'lodash'
export default {
  props:{
    selectItem:{ //刷选项目
      type:Array,
      required:true
    }
  },
  data() {
    return{
      show:false,
      selectObj:{},
    }
  },
  methods:{
    handleClose(val){
      if(val) {
        this.selectObj = lodash.cloneDeep(val)
      }
      this.show = !this.show
    },
    //分解prop，有|为有子项
    changeSelectProp(prop, index) {
      return prop.includes('|') ? prop.split('|')[index]:prop
    },
    //单个选择
    itemClick(prop, value) {
      if(prop === 'time') {
        //不限，删除start，end
        if(!value) {
          this.$delete(this.selectObj, 'start')
          this.$delete(this.selectObj, 'end')
        } else { 
          this.$set(this.selectObj, 'start', this.nowTime)
          const { time } = this.timeItem.find(p => p.value === value)
          this.$set(this.selectObj, 'end', time)
        }
      } else if(value === null) { //不限，删除当前prop
        return this.$delete(this.selectObj, prop)
      }
      this.$set(this.selectObj, prop, value)
    },
    //提交
    submit() {
      this.$emit('submitSelect', this.selectObj)
      this.show=false
    }
  }
}
