import BiuniqueTitle from './BiuniqueTitle'
import BiuniqueThinkTank from './BiuniqueThinkTank'
import RightSelect from '@/components/Common/RightSelect'
import { Swiper, SwiperSlide } from 'vue-awesome-swiper'
import 'swiper/swiper-bundle.css'
import config from "@/config/common.config"
import { getCategoryList } from '@/api/biunique'
export default {
  components:{
    BiuniqueTitle,
    BiuniqueThinkTank,
    Swiper,
    SwiperSlide,
    RightSelect
  },
  props:{
    contentConfig:{
      type:Object,
      required:true
    }
  },
  data(){
    return {
      active:1,
      config,
      swiperOptions:{
        centeredSlides:true,
        slidesPerView:1.5,
        spaceBetween: 40,
      },
      selectItem:[],
      selectObj:{}
    }
  },
  created() {
    this.getCategoryList()
  },
  methods:{
    async getCategoryList () {
      const { data }  = await getCategoryList({parentId:0, type:this.contentConfig.key=== 'scientific' ? 3 : 2})
      if(data.code ===0){
        this.selectItem = [
          { label:'学科领域', type:'select', options:data.data.entries.map(p =>({...p, value:p.id})), prop:'categoryId' },
        ]
      }
    },
    handleClick() {
      this.$refs.RightSelect.handleClose(this.selectObj)
    },
    submitSelect(selectObj) {
      this.selectObj = selectObj
      if(selectObj.categoryId) {
        this.$router.push({
          path: `/biunique/${this.contentConfig.key=== 'scientific' ? 'scientificCategoryList':'paperCategoryList'}/${selectObj.categoryId}`
        })
      }
    }
  }
}
